<template>
    <li :class="{ 'open': open, 'active': active }">

        <a :id="id" :href="link" @click="handleLinkClick()">
            <i v-if="icon" :class="icon"></i>
            <span class="title">{{ title }}</span>
            <span v-if="hasChildren" class="arrow"></span>
        </a>

        <transition @enter="slideEnter" v-if="hasChildren" @leave="slideLeave" :css="false">
            <ul class="sub-menu" v-if="hasChildren" v-show="open">
                <slot></slot>
            </ul>
        </transition>

    </li>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import {slide} from '../../app/codex/transitions';
    import {getRandomId} from '../../app/codex/utils'
    import Vue from 'vue'
    export default {
        name    : 'c-sidebar-item',
        props   : {
            item       : {type: Object},
            hasChildren: {type: Boolean, default: false},
            id         : {type: String, default: getRandomId()},
            href       : {type: String, default: 'javascript:;'},
            icon       : String,
            title      : String
        },
        data(){
            return {
                open  : false,
                active: false,
                link    : 'javascript:;'
            }
        },
        mounted(){
            if(!this.hasChildren) this.link = this.href;

            this.$eventHub.$on('sidebar:close-all-sub-menus', () => {
                this.closeSubMenu();
            });
            this.$eventHub.$on('sidebar:close-other-sub-menus', (sidebarItem) => {
                if ( this.isChild(sidebarItem) ) return;
                this.closeSubMenu();
            });
            this.$eventHub.$on('sidebar:active', () => {
                this.active = false;
            });
        },
        computed: {
            subMenuStyle(){
                return {'display': this.open ? 'block' : 'none'}
            }
        },
        watch   : {},
        methods : {
            ...slide,
            handleLinkClick(){
                if ( this.hasChildren ) {
                    this.toggleSubMenu();
                }
                let slideDuration = 300;
                setTimeout(() => {
                    let scrollOffset = 200;
                    let scrollTo = this.$$el.offset().top;
                    this.$scroll(scrollTo - scrollOffset, 400);
                }, slideDuration + 1);
            },
            setActive(){
                this.$eventHub.$emit('sidebar:active', this);
                this.active = true;
            },
            openSubMenu(){
                if ( this.hasChildren === false ) return;
                this.$eventHub.$emit('sidebar:close-other-sub-menus', this);
                this.open = true;
                this.$eventHub.$emit('sidebar:open-sub-menu', this)
            },
            closeSubMenu(){
                if ( this.hasChildren === false ) return;
                this.open = false;
                this.$eventHub.$emit('sidebar:close-sub-menu', this)
            },
            toggleSubMenu(){
                if ( this.hasChildren === false ) return;
                if ( this.open )
                    this.closeSubMenu();
                else
                    this.openSubMenu();
            },
            isTopLevel(){
                return this.$parent.$options.name !== this.$options.name;
            },
            getParents(){
                if ( this.isTopLevel() ) return [];
                let isTopLevel = false;
                let current = this;
                let parents = [];
                while (isTopLevel === false) {
                    parents.push(current.$parent);
                    isTopLevel = current.$parent.isTopLevel();
                    current = current.$parent;
                }
                return parents;
            },
            isChild(sidebarItem){
                return sidebarItem.getParents().filter(item => item.$vnode.key === this.$vnode.key).length === 1
            },
            setActiveAndOpenParents(){
                this.getParents().reverse().forEach(parent => {
                    parent.openSubMenu();
                })
                this.setActive();
            }
        }
    }
</script>





