<template>
    <aside class="c-sidebar" v-show="sidebar.hidden === false">
        <nav class="sidebar-inner" :style="innerStyle" ref="inner">
            <ul>
                <slot></slot>
            </ul>
        </nav>
    </aside>
</template>

<script>
    //https://github.com/chinchang/hint.css
    //    import tooltip from '../../components/tooltip.vue'
    import {mapGetters, mapActions} from 'vuex'
    //    import resize from 'mixins/resize';
    export default {
        name      : 'c-sidebar',
        components: {},
        props     : {items: Array, minHeight: Number, active: String},
        computed  : {
            ...mapGetters(['sidebar']),
            innerStyle(){
                if ( ! this.minHeight ) return {};
                return {'min-height': parseInt(this.minHeight) + 'px'}
            }
        },
        mounted(){
            if(this.active){
                this.resolveActive(this.$children);
            }
        },
        watch     : {},
        methods   : {
            ...mapActions(['toggleSidebar', 'closeSidebar', 'openSidebar', 'hideSidebar', 'showSidebar']),
            resolveActive(sidebarItems){
                sidebarItems.forEach(sidebarItem => {
                    if(sidebarItem.href === this.active){
                        sidebarItem.setActiveAndOpenParents();
                    } else if(sidebarItem.$children.length > 0){
                        this.resolveActive(sidebarItem.$children)
                    }
                });

            }
        }
    }
</script>



