<template>
    <div class="c-content" :style="outerStyle">
        <slot name="breadcrumb"></slot>
        <article class="document" v-if="html">
            <div v-html="html"></div>
        </article>
        <article class="document" v-else :style="innerStyle">
            <slot></slot>
        </article>
    </div>
</template>

<!--<style lang="scss" rel="stylesheet/scss">-->
<!--@import '../../../styles/base';-->
<!--@import '~prismjs/themes/prism.css';-->
<!--@import '~prismjs/plugins/autolinker/prism-autolinker.css';-->
<!--@import '~prismjs/plugins/line-numbers/prism-line-numbers.css';-->
<!--@import '~prismjs/plugins/show-language/prism-show-language.css';-->
<!--</style>-->


<script>
    import {mapGetters, mapActions} from 'vuex'
    import {resize, layout} from '../../app/codex/mixins'
//    import Prism from 'prismjs'

    export default {
        name    : 'c-content',
        mixins  : [resize, layout],
        props   : {
            html                   : {type: String},
            minHeight              : Number
        },
        data(){
            return {innerStyle: {}}
        },
        mounted(){
            this.isMd = this.isBreakpointUp('md');
            this.$on('resize', () => {
                this.isMd = this.isBreakpointUp('md')
                this.updateInnerStyle();
            });

            this.$nextTick(() => {
                this.prismHighlightAll();
                this.updateInnerStyle();
            })
        },
        watch   : {
            html(){
                this.$nextTick(() => {
                    this.prismHighlightAll();
                })
            },
            minHeight(){
                this.$nextTick(() => {
                    this.updateInnerStyle();
                })
            }
        },
        computed: {
            ...mapGetters(['sidebar']),
            outerStyle(){
                if ( ! this.minHeight ) return {};
                return {'min-height': parseInt(this.minHeight) + 'px'}
            },
//            innerStyle(){
//                if ( ! this.minHeight  ) return {};
//                let outerHeight = this.$$(this.$slots.breadcrumb[0].elm).outerHeight(true);
//                console.log('innerStyle', this.$slots.breadcrumb, {outerHeight});
//                return {'min-height': parseInt(this.minHeight - outerHeight) + 'px'}
//            }
        },
        methods : {
            prismHighlightAll(){
                if(window['Prism'] !== undefined) {
                    window['Prism'].highlightAll();
                }
            },
            updateInnerStyle(){
                if ( ! this.minHeight) {
                    this.innerStyle = {};
                    return;
                }
                let outerHeight = 0;
                if(this.$slots.breadcrumb && this.$slots.breadcrumb.length > 0){
                    outerHeight = this.$$(this.$slots.breadcrumb[0].elm).outerHeight(true);
                    console.log('innerStyle breadcrumb', this.$slots.breadcrumb, {outerHeight});
                }
                this.innerStyle = {'min-height': parseInt(this.minHeight - outerHeight) + 'px'}
            }
        }
    }
</script>




